import React, { useEffect, useState } from "react";
import "../styles/main/rules.scss";
import { CSSTransition } from "react-transition-group";
import { Link } from "react-router-dom";

const Rules = () => {
  const [show, setShow] = useState(false);

  useEffect(() => {
    setShow(true);
  }, []);
  return (
    <div className="rules">
      <div className="upper-part-wrap">
        <div className="upper-part">
          <div className="years">
            <div>
              1900
              <br />
              &emsp;-1953
            </div>
            <p> </p>
          </div>
          <div className="title">    &nbsp;</div>
          <div className="start-button">
            <Link to="/game">
              <button>
                <img
                  src={process.env.PUBLIC_URL + `/images/red-arrow.png`}
                  alt="arrow"
                />
              </button>
            </Link>

            <p> </p>
          </div>
        </div>
        <hr className="hr" />
      </div>
      <div className="description">
        <div className="left">
          <div className="content">
            <CSSTransition in={show} timeout={300} classNames="alert">
              <h3>
                &laquo;,
                <br />    1900 !&raquo;
              </h3>
            </CSSTransition>
            <p>
                  ,   
              &nbsp;  &nbsp;1900&nbsp;.
              &nbsp;    , 
              &nbsp;&nbsp;   
              &nbsp;&nbsp;  &nbsp;  
                  XX&nbsp;.
            </p>
          </div>
          <div className="line">
            <hr className="small-hr" />
          </div>
        </div>
        <div className="right">
          <Link
            to="/creators"
            style={{ textDecoration: "none" }}
            className="creators-link"
          >
            <button className="creators-button">
              
              <br />
              
            </button>
          </Link>
          <div>
            <h4 className="red">
                   
              <br />
              &nbsp;&nbsp;&nbsp;1953&nbsp;!
            </h4>
            <p>
                  &nbsp; . 
                  &nbsp; 
               ! !
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Rules;
